import React from "react";

type ElementItem = {
  type: "element" | "component";
  config: any;
  data: any;
  reactElement: React.ReactElement;
  properties: React.CSSProperties & {
    top: number;
    left: number;
    width: number;
    height: number;
  };
};

interface Elements {
  [key: string]: ElementItem;
}

interface ElementsAction {
  type: string;
  payload: any;
}

interface ReducerState {
  count: number;
  elements: Elements;
  checked?: string;
  backdrop: React.CSSProperties;
  dragging: boolean;
}

function reducer(state: ReducerState, action: ElementsAction) {
  switch (action.type) {
    case "add":
      const checked = state.count.toString();
      return {
        ...state,
        count: state.count + 1,
        checked,
        elements: Object.assign(state.elements, action.payload),
      };

    case "delete":
      const nextStateElements = { ...state.elements };
      delete nextStateElements[action.payload.key];
      return {
        ...state,
        count: state.count - 1,
        checked: undefined,
        elements: nextStateElements,
      };

    case "checked":
      return { ...state, checked: action.payload.key };

    case "update":
      const { key, draggingElement } = action.payload;
      return {
        ...state,
        count: state.count,
        checked: key,
        elements: { ...state.elements, ...{ [key]: draggingElement } },
      };

    case "backdrop":
      return {
        ...state,
        backdrop: {
          ...state.backdrop,
          ...action.payload,
        },
      };

    case "dragging":
      return {
        ...state,
        dragging: action.payload.dragging,
      };

    default:
      throw new Error();
  }
}

export { reducer };
export type { Elements, ElementItem, ReducerState };
