import { ReReactFC } from "../../../typings";

import { jsxCode, styleCode } from "./code";

interface SubtitleProps {
  config?: {
    total: number;
    unit: string;
    title: string;
  };
  style?: React.CSSProperties;
  className?: string;
}

const Subtitle: ReReactFC<SubtitleProps> = ({ config, style, className }) => {
  return (
    <div className={["wedget-subtitle", className].join(" ")} style={style}>
      <h3>
        {config?.total || 0}
        <span>{config?.unit || ""}</span>
      </h3>
      <h5>{config?.title || ""}</h5>
    </div>
  );
};

Subtitle.displayName = "Subtitle";
Subtitle.jsxCode = jsxCode;
Subtitle.styleCode = styleCode;

export default Subtitle;
