import type { ReducerState } from "../../reducers";

import React, { useEffect, useState } from "react";
import { PrismLight as SyntaxHighlighter } from "react-syntax-highlighter";
import { atomDark } from "react-syntax-highlighter/dist/esm/styles/prism";
import jsx from "react-syntax-highlighter/dist/esm/languages/prism/jsx";
import less from "react-syntax-highlighter/dist/esm/languages/prism/less";
import cn from "classnames";
import { saveAs } from "file-saver";
import JSZip from "jszip";
import Button from "../Button";

import { CompileCanvas } from "../../utils/CompileCanvas";

import styles from "./index.module.scss";

SyntaxHighlighter.registerLanguage("jsx", jsx);
SyntaxHighlighter.registerLanguage("less", less);

interface CodeTabsProps {
  state: ReducerState;
  visible: boolean;
}

interface CodeString {
  jsxCode: string;
  componentsCode: string;
  sampleCode: string;
  lessCode: string;
}

type TabItem = "JSX" | "COMPONENT" | "JSON" | "LESS";

const CodeTabs: React.FC<CodeTabsProps> = (props) => {
  const {
    state: { elements, backdrop },
    visible,
  } = props;
  const [active, setActive] = useState<number>(0);
  const [codeString, setCodeString] = useState<CodeString>({
    jsxCode: "",
    componentsCode: "",
    sampleCode: "",
    lessCode: "",
  });
  const tabs: Array<TabItem> = ["JSX", "COMPONENT", "JSON", "LESS"];

  useEffect(() => {
    if (visible) {
      const codeString = new CompileCanvas(elements, backdrop);
      setCodeString(codeString.getCodeString());
    }
  }, [backdrop, elements, visible]);

  const handleDownload = async () => {
    const fileName = "BILargeScreen";
    const zip = new JSZip();
    const codeFile = zip.folder(fileName.toString());
    if (codeFile) {
      const { jsxCode, componentsCode, sampleCode, lessCode } = codeString;
      if (jsxCode) {
        codeFile.file("index.tsx", jsxCode);
      }
      if (componentsCode) {
        codeFile.file("components.tsx", componentsCode);
      }
      if (sampleCode) {
        codeFile.file("data.ts", sampleCode);
      }
      if (lessCode) {
        codeFile.file("index.less", lessCode);
      }
      zip
        .generateAsync({ type: "blob" }, () => {})
        .then((content) => {
          saveAs(content, `${fileName}.zip`);
        });
    }
  };

  return (
    <div className={styles.body}>
      <div className={styles.nav}>
        <ul>
          {tabs.map((item, index) => (
            <li
              key={`tab-${index}`}
              className={cn({ [styles.active]: index === active })}
              onClick={() => setActive(index)}
            >
              {item}
            </li>
          ))}
        </ul>
        <Button
          style={{
            position: "absolute",
            right: 18,
            top: 9,
            cursor: "pointer",
          }}
          onClick={handleDownload}
        />
      </div>
      <div className={styles.content}>
        <ul style={{ transform: `translate(${-25 * active}%, 0)` }}>
          <li>
            <SyntaxHighlighter
              lineProps={{
                style: { wordBreak: "break-all", whiteSpace: "pre-wrap" },
              }}
              wrapLines={true}
              language="jsx"
              style={atomDark}
              showLineNumbers
            >
              {codeString.jsxCode}
            </SyntaxHighlighter>
          </li>
          <li>
            <SyntaxHighlighter
              lineProps={{
                style: { wordBreak: "break-all", whiteSpace: "pre-wrap" },
              }}
              wrapLines={true}
              language="jsx"
              style={atomDark}
              showLineNumbers
            >
              {codeString.componentsCode}
            </SyntaxHighlighter>
          </li>
          <li>
            <SyntaxHighlighter
              lineProps={{
                style: { wordBreak: "break-all", whiteSpace: "pre-wrap" },
              }}
              wrapLines={true}
              language="javascript"
              style={atomDark}
              showLineNumbers
            >
              {codeString.sampleCode}
            </SyntaxHighlighter>
          </li>
          <li>
            <SyntaxHighlighter language="less" style={atomDark} showLineNumbers>
              {codeString.lessCode}
            </SyntaxHighlighter>
          </li>
        </ul>
      </div>
    </div>
  );
};

export default CodeTabs;
