/* eslint-disable import/no-anonymous-default-export */
export default `interface LineOrdinaryProps {
  config?: Record<string, any>;
  data?: Record<string, any>[];
  style?: React.CSSProperties;
  className?: string;
}

const LineOrdinary: React.FC<LineOrdinaryProps> = ({ data = [], style, className }) => {
  return (
    <div className={className} style={style}>
      <Chart
        appendPadding={10}
        autoFit
        data={data}
        scale={{
          value: { min: 0, alias: "", type: "linear-strict" },
          year: { range: [0, 1] },
        }}
      >
        <Line position="year*value" />
        <Point position="year*value" />
        <Axis
          name="value"
          label={{
            formatter: (val) => val,
          }}
          grid={{
            line: {
              type: "line",
              style: {
                stroke: "#fff",
                lineWidth: 0.5,
                lineDash: [2, 2]
              },
            },
          }}
        />
        <Tooltip showCrosshairs />
      </Chart>
    </div>
  );
};`;
