const jsxCode = `interface SquareTagProps {
  config?: {
    type: 'right' | 'left' | 'double'
    leftTitle?: string;
    rightTitle?: string;
  };
  style?: React.CSSProperties;
  className?: string;
}

const SquareTag: ReReactFC<SquareTagProps> = ({ config, style, className }) => {
  const { type, leftTitle, rightTitle } = config || {};

  return (
    <div className={['square', className].join(' ')} style={style}>
      {(type === 'right' || type === 'double') && <span className="right">{rightTitle || ""}</span>}
      {(type === 'left' || type === 'double') && <span className="left">{leftTitle || ""}</span>}
    </div>
  );
};`;

const styleCode = ` .square {
    overflow: hidden;

    span {
      background: rgba(3, 227, 240, 0.2);
      font-family: PingFangSC-Medium;
      font-size: 20px;
      color: #ffffff;
      text-shadow: 0 1px 15px #03e3f0;
      font-weight: 500;
      height: 40px;
      padding: 0 32px;
      position: absolute;
      display: flex;
      flex-flow: row nowrap;
      align-items: center;
      justify-content: center;

      &.right {
        right: 0;
        top: 0;
        border-radius: 0 0 0px 4px;
      }

      &.left {
        left: 0;
        top: 0;
        border-radius: 0 0 4px 0;
      }
    }
  }`;

export { jsxCode, styleCode };
