const jsxCode = `interface SquareListProps {
  config?: {
    title: string;
  };
  data?: Record<string, any>;
  style?: React.CSSProperties;
  className?: string;
}

const SquareList: ReReactFC<SquareListProps> = ({
  config,
  data = [],
  style,
  className
}) => {
  return (
    <div className={['square-list', className].join(' ')} style={style}>
      <div className='title'>{config?.title || ""}</div>
      {data && data.length && (
        <ul>
          {data.map(({ area, total }: any, index: number) => (
            <li key={'square-list-' + index}>
              <span>{index + 1}</span>
              <span>{area}</span>
              <span>
                {total.toLocaleString("zh-Hans-CN", {
                  minimumFractionDigits: 0,
                  maximumFractionDigits: 2,
                })}
              </span>
            </li>
          ))}
        </ul>
      )}
    </div>
  );
};`;

const styleCode = ` .square-list {
    overflow: hidden;

    .title {
      background: rgba(3, 227, 240, 0.2);
      border-radius: 4px 0 4px 0;
      font-family: PingFangSC-Medium;
      font-size: 20px;
      color: #ffffff;
      text-shadow: 0 1px 15px #03e3f0;
      font-weight: 500;
      height: 40px;
      padding: 0 32px;
      position: absolute;
      left: 0;
      top: 0;
      display: flex;
      flex-flow: row nowrap;
      align-items: center;
      justify-content: center;
    }

    ul,
    li {
      list-style: none;
      margin: 0;
      padding: 0;
      width: 100%;
      display: flex;
    }

    ul {
      padding-top: 48px;
      flex-flow: column nowrap;
      align-items: flex-start;
      justify-content: flex-start;
    }

    li {
      height: 44px;
      flex-flow: row nowrap;
      align-items: center;
      justify-content: flex-start;

      span {
        font-family: PingFangSC-Regular;
        font-size: 20px;
        color: rgba(255, 255, 255, 0.85);
        letter-spacing: 0;
        line-height: 22px;
        font-weight: 400;

        &:nth-of-type(1) {
          flex-basis: 60px;
          text-align: center;
          color: #03e3f0;
        }

        &:nth-of-type(2) {
          flex-basis: 100px;
          text-align: left;
        }

        &:nth-of-type(3) {
          flex: 1;
          text-align: left;
        }
      }

      &:nth-of-type(n + 4) span:nth-of-type(1) {
        color: rgba(255, 255, 255, 0.85);
      }

      &:nth-of-type(odd) {
        background: rgba(236, 255, 255, 0.1);
      }
    }
  }`;

export { jsxCode, styleCode };
