import styles from "./index.module.scss";

interface IProps {
  type: "401" | "403" | "404" | "500";
  message: string;
}

function Exception(props: IProps) {
  const { type, message } = props;

  return (
    <div className={styles.error}>
      <h1>{message}</h1>
      <div className={styles.pesan}>{type}</div>
    </div>
  );
}

export default Exception;
