import { ReReactFC } from "../../../typings";

import { jsxCode, styleCode } from "./code";

interface SquareTagProps {
  config?: {
    type: "right" | "left" | "double";
    leftTitle?: string;
    rightTitle?: string;
  };
  style?: React.CSSProperties;
  className?: string;
}

const SquareTag: ReReactFC<SquareTagProps> = ({ config, style, className }) => {
  const { type, leftTitle, rightTitle } = config || {};

  return (
    <div className={["square", className].join(" ")} style={style}>
      {(type === "right" || type === "double") && (
        <span className="right">{rightTitle || ""}</span>
      )}
      {(type === "left" || type === "double") && (
        <span className="left">{leftTitle || ""}</span>
      )}
    </div>
  );
};

SquareTag.displayName = "SquareTag";
SquareTag.jsxCode = jsxCode;
SquareTag.styleCode = styleCode;

export default SquareTag;
