import { ReReactFC } from "../../../typings";

import { jsxCode, styleCode } from "./code";

interface SquareListProps {
  config?: {
    title: string;
  };
  data?: Record<string, any>;
  style?: React.CSSProperties;
  className?: string;
}

const SquareList: ReReactFC<SquareListProps> = ({
  config,
  data = [],
  style,
  className,
}) => {
  return (
    <div className={["square-list", className].join(" ")} style={style}>
      <div className="title">{config?.title || ""}</div>
      {data && data.length && (
        <ul>
          {data.map(({ area, total }: any, index: number) => (
            <li key={"square-list-" + index}>
              <span>{index + 1}</span>
              <span>{area}</span>
              <span>
                {total.toLocaleString("zh-Hans-CN", {
                  minimumFractionDigits: 0,
                  maximumFractionDigits: 2,
                })}
              </span>
            </li>
          ))}
        </ul>
      )}
    </div>
  );
};

SquareList.displayName = "SquareList";
SquareList.jsxCode = jsxCode;
SquareList.styleCode = styleCode;

export default SquareList;
