import { ReReactFC } from "../../../typings";

import { jsxCode, styleCode } from "./code";

interface TitleProps {
  config?: {
    title: string;
  };
  style?: React.CSSProperties;
  className?: string;
}

const Title: ReReactFC<TitleProps> = ({ config, style, className }) => {
  return (
    <div className={["wedget-title", className].join(" ")} style={style}>
      {config?.title || ""}
    </div>
  );
};

Title.displayName = "Title";
Title.jsxCode = jsxCode;
Title.styleCode = styleCode;

export default Title;
