/* eslint-disable import/no-anonymous-default-export */
const jsxCode = `interface PieSplitProps {
  config?: Record<string, any>;
  data?: Record<string, any>;
  style?: React.CSSProperties;
  className?: string;
}

const PieSplit: ReReactFC<PieSplitProps> = ({
  data = [],
  style,
  className,
}) => {
  const cols = {
    value: {
      formatter: (val: any) => {
        val = val * 100 + "%";
        return val;
      },
    },
  };

  return (
    <div className={className} style={style}>
      <Chart data={data} scale={cols} autoFit>
        <Legend
          name={"type"}
          visible
          layout="vertical"
          itemHeight={3}
          itemName={{
            formatter: (text, _item, index) => text + ": " + data[index].count,
          }}
        />
        <Coordinate type="theta" radius={0.8} innerRadius={0.65} />
        <Axis visible={false} />
        <Tooltip showTitle={false} />
        <Interval
          adjust="stack"
          position="value"
          color="type"
          shape="sliceShape"
          label={[
            "value",
            {
              offset: -3,
              style: {
                fill: "#FFFFFF",
                fontSize: 10,
              },
            },
          ]}
        />
      </Chart>
    </div>
  );
};`;

export { jsxCode };
