import type { Elements } from "../reducers";

import React from "react";
import { plugins, format as prettyFormat } from "pretty-format";

const { ReactElement } = plugins;

class CompileCanvas {
  private components: string[] = [];
  private sample: string[] = [];
  private jsxCode: string = "";
  private jsxBodyCode: string = "";
  private componentsCode: string = "";
  private sampleCode: string = "";
  private lessCode: string = "";
  private globalLessCode: string = "";
  private basisWidth: number = 1;

  constructor(resouce: Elements, backdrop: React.CSSProperties) {
    this.basisWidth = Number(backdrop.width);
    const wrapperStyles: React.CSSProperties = {
      ...backdrop,
      width: "100vw",
      height: "100vh",
      position: "relative",
    };
    this.lessCode += this.getClassCode("wrapper", wrapperStyles);
    this.jsxBodyCode += `\nconst BILargeScreen: React.FC = () => {\r\n`;
    this.jsxBodyCode += `  return (\n`;
    this.jsxBodyCode += `    <div className={styles.wrapper}>`;

    Object.entries(resouce).forEach(
      ([key, { type, config, data, reactElement, properties }]) => {
        // @ts-ignore
        const { displayName, jsxCode, styleCode } = reactElement.type;
        if (
          displayName &&
          jsxCode &&
          type === "component" &&
          !this.components.includes(displayName)
        ) {
          this.components.push(displayName);
          this.componentsCode += `\n${jsxCode.replaceAll(
            "ReReactFC",
            "React.FC"
          )}\n`;
          if (styleCode) {
            this.globalLessCode += `${
              this.globalLessCode ? "\n\n" : ""
            }${styleCode}`;
          }
        }
        if (data) {
          this.sample.push(`json_${key}`);
          this.sampleCode += `const json_${key} = ${JSON.stringify(data)};\n\n`;
        }
        this.jsxBodyCode += "\n      ";
        this.jsxBodyCode += prettyFormat(
          React.cloneElement(reactElement, {
            className: `styles.${type}_${key}`,
            config: config ? JSON.stringify(config) : undefined,
            data: data ? `json_${key}` : undefined,
          }),
          {
            min: true,
            plugins: [ReactElement],
            printFunctionName: false,
            escapeString: false,
          }
        )
          .replaceAll('="', "={")
          .replaceAll('" ', "} ");
        this.lessCode += this.getClassCode(`${type}_${key}`, {
          ...properties,
          position: "absolute",
        });
      }
    );
    this.jsxCode += this.getImportCode();
    this.jsxCode += this.jsxBodyCode;
    this.jsxCode += `\n    </div>\n`;
    this.jsxCode += `  )\n}\n\nexport default BILargeScreen;\n`;
    if (this.components.length) {
      const prevComponentsCode = this.componentsCode;
      this.componentsCode = "";
      if (prevComponentsCode.indexOf("new Scene") > -1) {
        this.componentsCode += `import { useEffect, useRef } from "react";\n`;
        this.componentsCode += `import { Scene, HeatmapLayer } from "@antv/l7";\n`;
        this.componentsCode += `import { Mapbox } from "@antv/l7-maps";\n`;
      }
      if (prevComponentsCode.indexOf("<Chart") > -1) {
        this.componentsCode += `import { Area, Axis, Chart, Coordinate, Interval, Interaction, Legend, Line, Point, Tooltip } from "bizcharts";\n`;
      }
      this.componentsCode += prevComponentsCode;
      this.componentsCode += `\nexport { ${this.components.join(", ")} }\n`;
    }
    if (this.sample.length) {
      this.sampleCode += `export { ${this.sample.join(", ")} };\n`;
    }
    if (this.globalLessCode) {
      this.lessCode += `:global {\n`;
      this.lessCode += this.globalLessCode;
      this.lessCode += `\n}`;
    }
  }

  protected transformValue = (
    key: string,
    value: any,
    CSSProperties: React.CSSProperties
  ) => {
    const { width } = CSSProperties;
    if (typeof value === "number") {
      switch (key) {
        case "width":
          return `${(value / this.basisWidth) * 100}vw`;

        case "height":
          return `${
            (Number(width) / this.basisWidth) * (value / Number(width)) * 100
          }vw`;

        case "left":
          return value ? `${(value / this.basisWidth) * 100}vw` : 0;

        case "top":
          return value ? `${(value / this.basisWidth) * 100}vw` : 0;

        default:
          return `${value * 2}px`;
      }
    } else {
      return value;
    }
  };

  protected transformReactCSSProperties = (property: string) => {
    let CSSProperties = "";
    for (const c of property) {
      CSSProperties += c.charCodeAt(0) < 97 ? `-${c.toLocaleLowerCase()}` : c;
    }
    return CSSProperties;
  };

  protected generateCssProperty = (
    key: string,
    value: any,
    CSSProperties: React.CSSProperties
  ) => {
    return `  ${this.transformReactCSSProperties(key)}: ${this.transformValue(
      key,
      value,
      CSSProperties
    )};`;
  };

  protected getClassCode = (
    className: string,
    CSSProperties: React.CSSProperties
  ) => {
    let classCode = `.${className} {\n`;
    classCode += Object.entries(CSSProperties)
      .filter(([key, value]) => value || key === "top" || key === "left")
      .map(([key, value]) =>
        this.generateCssProperty(key, value, CSSProperties)
      )
      .join("\n");
    classCode += `\n};\n\n`;
    return classCode;
  };

  protected getImportCode = () => {
    let importCode = `import React from "react";\n`;
    if (this.components && this.components.length) {
      importCode += `import { ${this.components.join(
        ", "
      )} } from "./components";\n`;
    }
    if (this.sample && this.sample.length) {
      importCode += `import { ${this.sample.join(", ")} } from "./data";\n`;
    }
    importCode += `\nimport styles from "./index.less";\n`;
    return importCode;
  };

  public getCodeString() {
    return {
      jsxCode: this.jsxCode,
      componentsCode: this.componentsCode,
      sampleCode: this.sampleCode,
      lessCode: this.lessCode,
    };
  }
}

export { CompileCanvas };
