/* eslint-disable import/no-anonymous-default-export */
export default `interface PieTooltipProps {
  config?: Record<string, any>;
  data?: Record<string, any>;
  style?: React.CSSProperties;
  className?: string;
}

const PieTooltip: ReReactFC<PieTooltipProps> = ({ data = [], style, className }) => {
  const cols = {
    value: {
      formatter: (val: any) => {
        val = val * 100 + "%";
        return val;
      },
    },
  };

  return (
    <div className={className} style={style}>
      <Chart data={data} scale={cols} autoFit>
        <Legend visible={false} />
        <Coordinate type="theta" radius={0.8} innerRadius={0.65} />
        <Axis visible={false} />
        <Tooltip showTitle={false} />
        <Interval
          adjust="stack"
          position="value"
          color="type"
          shape="sliceShape"
          label={[
            "count",
            {
              layout: { type: "limit-in-plot", cfg: { action: "ellipsis" } },
              content: ({ type, count }) => {
                return type + ': ' + count + '';
              },
            },
          ]}
        />
        <Interaction type="element-single-selected" />
      </Chart>
    </div>
  );
};`;
