import type { Elements, ReducerState } from "./reducers";
import type { IconProps } from "./components/Icon";

import { useEffect, useReducer, useRef, useState } from "react";
import cn from "classnames";
import { CodeTabs, Drawer, Icon, RulerCanvas } from "./components";
import { reducer } from "./reducers";
import Widgets from "./widgets";

import styles from "./assets/scss/app.module.scss";

function App() {
  const initialState: ReducerState = {
    count: 0,
    elements: {},
    backdrop: {
      width: 960,
      height: 540,
      backgroundColor: "rgb(35, 38, 48)",
      backgroundImage: "",
      backgroundSize: "contain",
      backgroundRepeat: "no-repeat",
    },
    dragging: false,
  };
  const [state, dispatch] = useReducer(reducer, initialState);
  const [select, setSelect] = useState<keyof typeof Widgets>("square");
  const [collapsed, setCollapsed] = useState(false);
  const [visible, setVisible] = useState<boolean>(false);
  const { dragging } = state;
  const isFileFromOS = useRef(true);
  const cachedTarget = useRef<EventTarget | null>(null);
  const hasDraggedFileFromBrowserOutsideOfWindow = useRef(false);

  useEffect(() => {
    const handleDragEnter = (e: DragEvent) => {
      cachedTarget.current = e.target;
    };
    const handleDragStart = (e: DragEvent) => {
      // @ts-ignore
      const widgetKey = e.target?.dataset?.key;
      e.dataTransfer?.setData("widget-key", widgetKey);
      isFileFromOS.current = false;
      hasDraggedFileFromBrowserOutsideOfWindow.current = false;
    };

    const handleDragOver = (e: DragEvent) => {
      e.preventDefault();

      if (
        hasDraggedFileFromBrowserOutsideOfWindow.current &&
        document.hasFocus()
      ) {
      } else if (isFileFromOS.current) {
        hasDraggedFileFromBrowserOutsideOfWindow.current = false;
      } else {
        if (!dragging) {
          dispatch({ type: "dragging", payload: { dragging: true } });
        }
      }
    };

    const handleDragLeave = (e: DragEvent) => {
      if (e.target === cachedTarget.current) {
        if (
          isFileFromOS.current &&
          !hasDraggedFileFromBrowserOutsideOfWindow.current
        ) {
          if (dragging) {
            dispatch({ type: "dragging", payload: { dragging: false } });
          }
        } else {
          hasDraggedFileFromBrowserOutsideOfWindow.current = true;
          isFileFromOS.current = true;
        }
      }
    };

    const handleDrop = (e: DragEvent) => {
      const widgetKey = e.dataTransfer?.getData("widget-key");
      e.preventDefault();
      const {
        type,
        sample: { data, config },
        reactElement,
        properties,
      } = Widgets[select][widgetKey || "text"];
      const {
        width = 0,
        height = 0,
        borderRadius = 0,
        fontSize = 0,
      } = properties;
      isFileFromOS.current = true;
      hasDraggedFileFromBrowserOutsideOfWindow.current = false;
      const nextProperties = {
        ...properties,
        left: e.offsetX - width / 4,
        top: e.offsetY - height / 4,
        width: width / 2,
        height: height / 2,
      };
      if (borderRadius) {
        nextProperties.borderRadius = borderRadius / 2;
      }
      if (fontSize) {
        nextProperties.fontSize = fontSize / 2;
      }
      const payload: Elements = {
        [state.count.toString()]: {
          type,
          data,
          config,
          reactElement,
          properties: nextProperties,
        },
      };

      dispatch({ type: "add", payload });
      if (dragging) {
        dispatch({ type: "dragging", payload: { dragging: false } });
      }
    };

    window.addEventListener("dragenter", handleDragEnter);
    window.addEventListener("dragstart", handleDragStart);
    window.addEventListener("dragover", handleDragOver);
    window.addEventListener("dragleave", handleDragLeave);
    window.addEventListener("drop", handleDrop);

    return () => {
      window.removeEventListener("dragenter", handleDragEnter);
      window.removeEventListener("dragstart", handleDragStart);
      window.removeEventListener("dragover", handleDragOver);
      window.removeEventListener("dragleave", handleDragLeave);
      window.removeEventListener("drop", handleDrop);
    };
  }, [dragging, select, state.count]);

  const menus: Array<{ type: IconProps["type"]; fontSize: number }> = [
    {
      type: "square",
      fontSize: 18,
    },
    {
      type: "text",
      fontSize: 25,
    },
    {
      type: "pie",
      fontSize: 23,
    },
    {
      type: "line",
      fontSize: 23,
    },
    {
      type: "map",
      fontSize: 23,
    },
  ];

  return (
    <div className={styles.container}>
      <div className={styles.tool}>
        <span className={styles.logo}></span>
        <span className={styles.tips}>
          
        </span>
        <span className={styles.code} onClick={() => setVisible(!visible)}>
          <Icon type="code" style={{ fontSize: 26 }} />
        </span>
      </div>
      <div className={styles.content}>
        <div className={cn(styles.sider, { [styles.unflod]: collapsed })}>
          <div className={styles.menus}>
            <ul>
              {menus.map(({ type, fontSize }) => (
                <li
                  key={type}
                  className={cn({ [styles.active]: type === select })}
                  onClick={() => {
                    setSelect(type);
                    setCollapsed(true);
                  }}
                >
                  <Icon type={type} style={{ fontSize }} />
                </li>
              ))}
            </ul>
            <div className={styles.charts}>
              {Object.entries(Widgets[select]).map(([key, { sample }]) => (
                <div key={key} className={styles.item}>
                  <img src={sample.graph} alt={key} />
                  <div
                    draggable
                    data-key={key}
                    style={{ backgroundImage: `url(${sample.graph})` }}
                  />
                </div>
              ))}
            </div>
          </div>
          <div className={styles.flod} onClick={() => setCollapsed(!collapsed)}>
            <Icon type="menu" style={{ fontSize: 22, color: "#666666" }} />
          </div>
        </div>
        <div className={styles.edit}>
          <RulerCanvas state={state} dispatch={dispatch} />
        </div>
      </div>
      <Drawer visible={visible} onClose={() => setVisible(false)}>
        <CodeTabs state={state} visible={visible} />
      </Drawer>
    </div>
  );
}

export default App;
