import type { DraggableData, DraggableEvent } from "react-draggable";
import type { ResizeDirection } from "re-resizable";
import type { ReducerState, ElementItem } from "../../reducers";

import React, { useCallback, useEffect, useState } from "react";
import { Ruler } from "react-visual-components";
import { Position, ResizableDelta, Rnd } from "react-rnd";
import cn from "classnames";
import Contextmenu from "../Contextmenu";
import Setting from "../Setting";

import styles from "./index.module.scss";
import Icon from "../Icon";

interface IProps {
  state: ReducerState;
  dispatch: any;
}

interface spacingOffset {
  lineStyle: React.CSSProperties;
  spanStyle: React.CSSProperties;
}

function RulerCanvas(props: IProps) {
  const { state, dispatch } = props;
  const { checked, elements, backdrop, dragging } = state;
  const [resize, setResize] = useState<boolean>(false);
  const [visible, setVisible] = useState<boolean>(false);
  const [draggingElement, setDraggingElement] = useState<ElementItem>();
  const [spacingOffset, setSpacingOffset] = useState<spacingOffset[]>([]);

  const handleKeyDown = useCallback(
    (event: { key: any }) => {
      const { key } = event;
      if (key.toLocaleLowerCase() === "backspace" && checked && !visible) {
        dispatch({ type: "delete", payload: { key: checked } });
      }
    },
    [checked, dispatch, visible]
  );

  const handleKeyUp = useCallback((event: { key: any }) => {
    // eslint-disable-next-line @typescript-eslint/no-unused-vars
    const { key } = event;
  }, []);

  useEffect(() => {
    document.addEventListener("keydown", handleKeyDown);
    document.addEventListener("keyup", handleKeyUp);

    return () => {
      document.removeEventListener("keydown", handleKeyDown);
      document.removeEventListener("keyup", handleKeyUp);
    };
  });

  const onResizeStart = (
    _e: React.MouseEvent<HTMLElement> | React.TouchEvent<HTMLElement>,
    _dir: ResizeDirection,
    _elementRef: HTMLElement
  ) => {
    setResize(true);
  };

  const onResizeStop = (
    _e: MouseEvent | TouchEvent,
    _direction: ResizeDirection,
    _elementRef: HTMLElement,
    delta: ResizableDelta,
    key: string
  ) => {
    setResize(false);
    dispatch({
      type: "update",
      payload: {
        key,
        draggingElement: {
          ...elements[key],
          properties: {
            ...elements[key].properties,
            width: elements[key].properties.width + delta.width,
            height: elements[key].properties.height + delta.height,
          },
        },
      },
    });
  };

  const onResize = (
    _e: MouseEvent | TouchEvent,
    _dir: ResizeDirection,
    _elementRef: HTMLElement,
    delta: ResizableDelta,
    position: Position,
    key: string
  ) => {
    dispatch({
      type: "update",
      payload: {
        key,
        draggingElement: {
          ...elements[key],
          properties: {
            ...elements[key].properties,
            left: position.x,
            top: position.y,
          },
        },
      },
    });
  };

  const onStart = (key: string) => {
    setResize(true);
    setDraggingElement(elements[key]);
  };

  const onStop = (key: string) => {
    setResize(false);
    setSpacingOffset([]);
    setDraggingElement(undefined);
    dispatch({ type: "update", payload: { key, draggingElement } });
  };

  const calculateGuides = (key: string) => {
    if (draggingElement) {
      const currElements = { ...elements, [key]: draggingElement };
      const { top: startY, left: startX } = currElements[key].properties;
      const intersectionStartY = Object.values(currElements)
        .filter(
          ({ properties: { top, height } }) =>
            top === startY || top + height === startY
        )
        .sort((a, b) => a.properties.left - b.properties.left);

      const intersectionStartX = Object.values(currElements)
        .filter(
          ({ properties: { left, width } }) =>
            left === startX || left + width === startX
        )
        .sort((a, b) => a.properties.top - b.properties.top);

      const nextSpacingsY: spacingOffset[] = [];
      const nextSpacingsX: spacingOffset[] = [];

      if (intersectionStartY.length > 1) {
        intersectionStartY.forEach(
          ({ properties: { left, width } }, index, arr) => {
            if (arr[index + 1]) {
              const leftWith = left + width;
              nextSpacingsY.push({
                lineStyle: {
                  left: leftWith,
                  width: arr[index + 1].properties.left - leftWith,
                  top: startY,
                  height: 1,
                  flexDirection: "row",
                },
                spanStyle: {
                  top: 0,
                },
              });
            }
          }
        );
      }

      if (intersectionStartX.length > 1) {
        intersectionStartX.forEach(
          ({ properties: { top, height } }, index, arr) => {
            if (arr[index + 1]) {
              const topHeight = top + height;
              nextSpacingsX.push({
                lineStyle: {
                  top: topHeight,
                  height: arr[index + 1].properties.top - topHeight,
                  left: startX,
                  width: 1,
                  flexDirection: "column",
                },
                spanStyle: {
                  left: 3,
                },
              });
            }
          }
        );
      }
      setSpacingOffset([...nextSpacingsY, ...nextSpacingsX]);
    }
  };

  const handleDrag = (_e: DraggableEvent, ui: DraggableData, key: string) => {
    if (draggingElement) {
      setDraggingElement({
        ...elements[key],
        properties: {
          ...elements[key].properties,
          left: draggingElement.properties.left + ui.deltaX,
          top: draggingElement.properties.top + ui.deltaY,
        },
      });
      calculateGuides(key);
    }
  };

  const handleDeselect = (e: React.MouseEvent<HTMLDivElement>) => {
    e.stopPropagation();
    dispatch({ type: "checked", payload: { key: undefined } });
  };

  return (
    <>
      <div className={styles.ruler}>
        <div className={styles.aided}>
          <Icon type="eye" style={{ color: "#515151", fontSize: 15 }} />
        </div>
        <Ruler
          width={3000}
          height={20}
          direction="horizontal"
          className={styles.abscissa}
        />
        <Ruler
          width={20}
          height={3000}
          direction="vertical"
          className={styles.ordinate}
        />
        <div
          className={styles.container}
          id="container"
          onClick={handleDeselect}
        >
          <div
            className={styles.backdrop}
            style={{
              ...backdrop,
              width: backdrop?.width,
              height: backdrop?.height,
            }}
          >
            {dragging && <div className={styles.layer} />}
            <div className={styles.guides}>
              {draggingElement ? (
                <div
                  className={styles.line}
                  style={{
                    width: draggingElement.properties.left,
                    height: draggingElement.properties.top,
                  }}
                >
                  <span>{`${draggingElement.properties.left * 2}, ${
                    draggingElement.properties.top * 2
                  }`}</span>
                </div>
              ) : null}
            </div>
            {spacingOffset.length > 0 &&
              spacingOffset.map(
                (item, index) =>
                  Number(item?.lineStyle?.height) > 0 &&
                  Number(item?.lineStyle?.width) > 0 && (
                    <div
                      key={`spaceing-offset-${index}`}
                      className={cn(
                        styles.spacing,
                        {
                          [styles.vertical]:
                            Number(item?.lineStyle?.height) > 1,
                        },
                        {
                          [styles.horizon]:
                            Number(item?.lineStyle?.height) <= 1,
                        }
                      )}
                      style={item.lineStyle}
                    >
                      <span style={item.spanStyle}>
                        {Number(item?.lineStyle?.height) > 1
                          ? item.lineStyle.height
                          : item.lineStyle.width}
                      </span>
                    </div>
                  )
              )}
            {Object.entries(elements).map(
              ([key, { data, config, reactElement, properties }]) => (
                <Rnd
                  key={`draggable-${key}`}
                  bounds="parent"
                  position={{
                    x: properties.left,
                    y: properties.top,
                  }}
                  size={{ width: properties.width, height: properties.height }}
                  style={{
                    transition: resize ? undefined : "all 0.45s ease-in-out",
                  }}
                  scale={1}
                  onClick={(e: React.MouseEvent<HTMLDivElement>) =>
                    e.stopPropagation()
                  }
                  onDoubleClick={(e: React.MouseEvent<HTMLDivElement>) => {
                    e.stopPropagation();
                    setVisible(!visible);
                  }}
                  onMouseDown={() =>
                    dispatch({ type: "checked", payload: { key } })
                  }
                  onDragStart={() => onStart(key)}
                  onDragStop={() => onStop(key)}
                  onDrag={(e, ui) => handleDrag(e, ui, key)}
                  onResizeStart={onResizeStart}
                  onResizeStop={(
                    e: MouseEvent | TouchEvent,
                    dir: ResizeDirection,
                    elementRef: HTMLElement,
                    delta: ResizableDelta
                  ) => onResizeStop(e, dir, elementRef, delta, key)}
                  onResize={(
                    e: MouseEvent | TouchEvent,
                    dir: ResizeDirection,
                    elementRef: HTMLElement,
                    delta: ResizableDelta,
                    position: Position
                  ) => onResize(e, dir, elementRef, delta, position, key)}
                >
                  <>
                    <div
                      className={cn(styles.checked, {
                        [styles.cur]: checked === key,
                      })}
                    >
                      <div className={styles.dots} />
                      <div className={styles.dots} />
                      <div className={styles.dots} />
                      <div className={styles.dots} />
                      <div className={styles.dots} />
                      <div className={styles.dots} />
                      <div className={styles.dots} />
                      <div className={styles.dots} />
                    </div>
                    {React.cloneElement(reactElement, {
                      data,
                      config,
                      style: properties,
                    })}
                  </>
                </Rnd>
              )
            )}
          </div>
        </div>
      </div>
      <Contextmenu
        targetId="container"
        menus={[
          {
            label: "Copy",
            key: ["", "C"],
          },
          {
            label: "Delete",
            key: ["", "", "D"],
            command: () =>
              dispatch({ type: "delete", payload: { key: checked } }),
          },
          {
            label: "Setting",
            key: ["", "", "S"],
            command: () => setVisible(true),
          },
        ]}
      />
      <Setting
        visible={visible}
        checked={checked}
        dispatch={dispatch}
        data={checked ? elements[checked].data : undefined}
        config={checked ? elements[checked].config : undefined}
        element={checked ? elements[checked] : undefined}
        properties={checked ? elements[checked].properties : backdrop}
        onClose={setVisible}
      />
    </>
  );
}

export default RulerCanvas;
